
import React, { Component } from 'react';
import { Dashboard } from "@uppy/react";
//import { Link } from 'react-router-dom';
//import { baseUrl } from '../../../../../Assets/AssetsHelper';


class AssetsUploader extends Component {
  state = {
    postfile: "",
    postId: null,
    mediaWidth: null,
    mediaHeight: null
  }

  componentWillUnmount() {
    // this.uppy.close();
  }
  componentDidMount() {
    const { uppyPostEdit } = this.props
    var self = this;
    uppyPostEdit.on('transloadit:result', (stepName, result) => {

      this.setState({ postId: result.id, postFile: `https://cloud.taggbox.com/onsiteupload/images/${result.id}`, mediaHeight: result.meta.height, mediaWidth: result.meta.width }, () => {
        setTimeout(() => {
          this.props.onUploadedData({ checked: false, postFile: this.state.postFile, postId: this.state.postId, addTags: "", caption: "", copyright: false, content: "", "mediaHeight": this.state.mediaHeight, "mediaWidth": this.state.mediaWidth })
          self.porps.uppyPostEdit.cancelAll()
        }, 300)
      })

    })
  }
  render() {

    return <div className="card mb-3">
      <div className="card-body text-decoration-none">
        <Dashboard
          onClick={() => this.uploadNextStep('editUploadedMedia')}

          inline={true}
          // replaceTargetContent={true}

          height={'100%'}
          width={'100%'}
          theme={'light'}

          // browserBackButtonClose={true}

          uppy={this.props.uppyPostEdit}
          plugins={["Webcam", "Instagram", "GoogleDrive", "Dropbox"]}
        />
      </div>
    </div>
  }
}

export default AssetsUploader;